html, body, div, span,
h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

ul {
  list-style: none; }

a:hover, button:hover, a:focus, button:focus {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #1B1C21; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

@font-face {
  font-family: 'ExtraBold';
  src: url(./fonts/TTLakes-ExtraBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-ExtraBold.eot) format("eot");
  src: url(./fonts/TTLakes-ExtraBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'DemiBold';
  src: url(./fonts/TTLakes-DemiBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-DemiBold.eot) format("eot");
  src: url(./fonts/TTLakes-DemiBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Regular';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  src: url(./fonts/TTLakes-Regular.eot) format("eot");
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

h2 {
  font-family: "Regular";
  font-size: 16px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  color: #7E7E82; }

.section {
  width: 100%; }

.section__title {
  background: linear-gradient(90deg, #F8F8F8 1.39%, #FFFFFF 100%);
  border-top: 1px solid #D9FFF5; }

.section__arrow {
  display: flex;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 2px solid #FF3E79;
  border-radius: 50%; }

.arrow {
  margin: 14px 17px; }

.section__link {
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 16px; }
  .section__link:hover {
    box-shadow: inset 0px 0px 5px 0.5px #FF3E79; }
  .section__link:focus {
    box-shadow: inset 0px 0px 5px 0.5px #FF3E79; }

.section__logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 40px;
  width: 128px; }

.img {
  object-fit: contain; }

.section__btn {
  margin-top: 24px; }

.more-btn {
  position: relative;
  font-family: "DemiBold"; }
  .more-btn::before {
    content: url(./static/strela.svg);
    position: absolute;
    left: 0px;
    top: -4px; }

.more-btn--clicked::before {
  transform: rotate(180deg) translateY(4px); }

@media (min-width: 320px) and (max-width: 767px) {
  .section__btn {
    display: none; }
  .swiper-pagination {
    position: relative;
    top: 16px; }
  .swiper {
    overflow-x: hidden;
    padding: 16px 0 66px 16px; }
    .swiper .swiper-pagination-bullet {
      background-color: #B5B6BC; }
  .section__title {
    padding: 16px; }
  .section__item {
    max-width: 240px; }
  .section__link {
    width: 208px; } }

@media (min-width: 405px) and (max-width: 450px) {
  .section__item, .section__ref {
    max-width: 260px; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .container {
    max-width: 768px;
    margin: 0 auto; }
  .swiper-pagination {
    display: none; }
  .section__list {
    display: grid;
    grid-template-columns: repeat(3, 232px);
    gap: 24px;
    max-height: 190px;
    overflow: hidden;
    transition: max-height 200ms; }
  .section__list--show {
    max-height: 1000px;
    transition: max-height 300ms; }
  .section__title {
    padding: 16px 24px; }
  .section__main {
    padding: 24px 0px 34px 0px; }
  .section__btn {
    padding-left: 33px; }
  .section__link {
    width: 192px; } }

@media (min-width: 1440px) {
  .container {
    max-width: 1120px;
    margin: 0 auto; }
  .swiper-pagination {
    display: none; }
  .section__list {
    display: grid;
    grid-template-columns: repeat(4, 240px);
    gap: 32px;
    max-height: 190px;
    overflow: hidden;
    transition: max-height 200ms; }
  .section__list--show {
    max-height: 1000px;
    transition: max-height 300ms; }
  .section__title {
    padding: 16px 0 16px 32px; }
  .section__main {
    padding: 24px 0px 34px 0px; }
  .section__btn {
    padding-left: 39px; }
  .section__link {
    width: 208px; } }

.section__ref {
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  padding: 16px 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 64px;
  max-width: 240px; }
  .section__ref:hover {
    box-shadow: inset 0px 0px 5px 0.5px #FF3E79; }
  .section__ref:focus {
    box-shadow: inset 0px 0px 5px 0.5px #FF3E79; }

.section__name {
  align-self: flex-start;
  font-family: "DemiBold";
  font-size: 16px;
  line-height: 24px; }
