html, body, div, span,
h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

ul {
  list-style: none; }

a:hover, button:hover, a:focus, button:focus {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #1B1C21; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

@font-face {
  font-family: 'ExtraBold';
  src: url(./fonts/TTLakes-ExtraBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-ExtraBold.eot) format("eot");
  src: url(./fonts/TTLakes-ExtraBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'DemiBold';
  src: url(./fonts/TTLakes-DemiBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-DemiBold.eot) format("eot");
  src: url(./fonts/TTLakes-DemiBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Regular';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  src: url(./fonts/TTLakes-Regular.eot) format("eot");
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

.sidebar__title {
  position: relative;
  font-family: "ExtraBold";
  font-size: 24px; }
  .sidebar__title::before {
    position: absolute;
    left: -15px;
    top: 0;
    content: "";
    height: 40px;
    width: 3px;
    background-color: #41F6D7;
    border: 1px solid #41F6D7;
    border-radius: 3px; }

.sidebar-feedback, .sidebar-connection {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  justify-content: flex-start;
  margin-left: auto; }

.form__body {
  display: flex;
  flex-direction: column;
  gap: 22px; }

.form__input {
  font-family: "Regular";
  color: #B5B6BC;
  font-size: 16px;
  padding: 12px 16px; }

textarea.form__input {
  resize: none;
  height: 118px; }

.sidebar-message, .sidebar-call {
  transition: 200ms;
  transform: translateX(100%);
  position: fixed;
  right: 0px;
  top: 0;
  z-index: 10; }

.sidebar-message--show, .sidebar-call--show {
  transition: 200ms;
  transform: translateX(0%); }

.form__order .order__text::after {
  right: -20px; }

@media (max-width: 767px) {
  .sidebar-feedback, .sidebar-connection {
    padding: 19px 16px 32px 16px;
    width: 320px; }
  .sidebar__title {
    top: 80px;
    left: -58px; }
  .form {
    margin-top: 76px; }
  .form__input {
    width: 288px; }
  .form__order {
    margin-left: 80px; }
  .form__body {
    display: flex;
    flex-direction: column;
    gap: 25px; } }

@media (min-width: 768px) {
  .sidebar-feedback, .sidebar-connection {
    padding: 19px 24px 32px 16px;
    margin-left: auto;
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), -16px 0px 52px rgba(14, 24, 80, 0.2);
    width: 440px; }
  .sidebar-message, .sidebar-call {
    background-color: rgba(255, 255, 255, 0.96);
    width: 100%; }
  .form__input {
    width: 400px; }
  .form__order {
    margin-left: 140px; }
  .form_warning {
    font-size: 14px;
    padding-right: 32px; }
  .sidebar__list {
    position: relative; }
  .sidebar-message .sidebar__item:first-child,
  .sidebar-call .sidebar__item:first-child {
    position: absolute;
    left: -65px; } }
