html, body, div, span,
h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

ul {
  list-style: none; }

a:hover, button:hover, a:focus, button:focus {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #1B1C21; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

@font-face {
  font-family: 'ExtraBold';
  src: url(./fonts/TTLakes-ExtraBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-ExtraBold.eot) format("eot");
  src: url(./fonts/TTLakes-ExtraBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'DemiBold';
  src: url(./fonts/TTLakes-DemiBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-DemiBold.eot) format("eot");
  src: url(./fonts/TTLakes-DemiBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Regular';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  src: url(./fonts/TTLakes-Regular.eot) format("eot");
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

.sidebar {
  padding: 19px 24px 32px 16px;
  height: 100vh;
  width: 320px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #FFFFFF; }

@media (min-width: 250px) and (max-width: 1439px) {
  .sidebar-burger {
    transition: 200ms;
    transform: translateX(-100%);
    position: fixed;
    left: 0px;
    top: 0;
    z-index: 10; }
  .sidebar-burger--show {
    transition: 200ms;
    transform: translateX(0%); } }

@media (min-width: 768px) and (max-width: 1439px) {
  .sidebar {
    box-shadow: -2px 0px 4px rgba(69, 79, 126, 0.02), 16px 0px 52px rgba(14, 24, 80, 0.2); }
  .sidebar-burger {
    background-color: rgba(255, 255, 255, 0.96);
    width: 100%; } }

@media (min-width: 1440px) {
  .sidebar__block {
    background-color: #FFFFFF;
    height: 100vh; }
  .sidebar-burger {
    position: absolute;
    left: -320px;
    top: 0;
    z-index: 10; }
  .container {
    left: 160px; }
  .sidebar__item:first-child {
    display: none; } }

.sidebar__nav {
  padding-bottom: 50px; }

.sidebar__list {
  display: flex;
  align-items: center;
  gap: 17px; }

.sidebar__item:nth-child(2n) {
  flex-grow: 1; }

.icon {
  background-color: #FF3E79;
  width: 40px;
  height: 38px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .icon:focus {
    cursor: pointer;
    opacity: 0.8; }
  .icon:hover {
    cursor: pointer;
    opacity: 0.8; }

.logo {
  display: block;
  width: 97px;
  height: 51px; }

.choice {
  font-family: "DemiBold";
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21; }

.choice__list {
  display: flex;
  flex-direction: column;
  gap: 32px; }

.choice__item {
  display: block; }

.choice__item--active {
  position: relative;
  font-family: "Regular";
  color: #7E7E82; }
  .choice__item--active::before {
    position: absolute;
    content: "";
    left: -15px;
    top: -5px;
    height: 32px;
    width: 3px;
    background-color: #41F6D7;
    border: 1px solid #41F6D7;
    border-radius: 2px; }

.choice__link:focus {
  cursor: pointer; }

.choice__link:hover {
  cursor: pointer; }

.other__buttons {
  display: flex;
  gap: 16px; }

.other__contacts {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding-top: 12px; }

.other__phone {
  font-family: "ExtraBold";
  font-size: 24px; }

.langs {
  display: flex;
  gap: 7px;
  padding-top: 40px; }

.langs__input {
  position: absolute;
  -moz-appearance: none;
       appearance: none; }
  .langs__input:hover + .langs__name {
    cursor: pointer; }
  .langs__input:focus + .langs__name {
    cursor: pointer; }
  .langs__input:checked + .langs__name {
    color: #7E7E82; }
