html, body, div, span,
h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

ul {
  list-style: none; }

a:hover, button:hover, a:focus, button:focus {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #1B1C21; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

@font-face {
  font-family: 'ExtraBold';
  src: url(./fonts/TTLakes-ExtraBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-ExtraBold.eot) format("eot");
  src: url(./fonts/TTLakes-ExtraBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'DemiBold';
  src: url(./fonts/TTLakes-DemiBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-DemiBold.eot) format("eot");
  src: url(./fonts/TTLakes-DemiBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Regular';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  src: url(./fonts/TTLakes-Regular.eot) format("eot");
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

.color {
  color: #FF3E79; }

.section__servises {
  padding-top: 16px;
  display: flex;
  flex-direction: column; }

.section__servise {
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  align-items: center; }

.section__info {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: flex-start; }

.section__offer {
  position: relative;
  font-family: "DemiBold";
  font-size: 16px; }

.section__offer::after {
  content: '';
  position: absolute;
  border-top: 2px solid #FF3E79;
  border-right: 2px solid #FF3E79;
  height: 6px;
  width: 6px;
  transform: rotate(45deg);
  top: 7px;
  left: 310px; }

.order__btn {
  padding-left: 12px;
  width: 107px;
  height: 32px;
  border-radius: 16px;
  background-color: #FF3E79;
  color: #FFFFFF;
  text-align: left; }

.order__text {
  font-size: 12px;
  letter-spacing: -0.15px;
  font-weight: bolder;
  text-transform: uppercase;
  position: relative; }

.order__text::after {
  content: '';
  position: absolute;
  border-top: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
  height: 8px;
  width: 8px;
  transform: rotate(45deg);
  top: 4px;
  right: -30px; }

.section__kind {
  font-size: 14px; }

@media (min-width: 320px) and (max-width: 767px) {
  .section__head {
    display: none; }
  .swiper-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap; }
  .section__servise {
    border-radius: 6px;
    display: grid;
    grid-template-columns: 100px 120px;
    grid-template-areas: "kind kind" "price price" "time orde";
    gap: 16px;
    width: 250px;
    height: 200px;
    padding: 16px; }
  .section__stuff {
    color: #7E7E82;
    font-size: 14px; }
  .section__kind {
    display: flex;
    flex-direction: column;
    gap: 4px;
    grid-area: kind; }
  .section__price {
    display: flex;
    flex-direction: column;
    gap: 4px;
    grid-area: price; }
  .section__time {
    display: flex;
    flex-direction: column;
    gap: 4px;
    grid-area: time; }
  .section__order {
    grid-area: orde; }
  .swiper-pagination {
    position: relative;
    top: 16px; }
  .section__info {
    padding: 0 16px 40px 16px; }
  .section__nds {
    text-align: left; }
  .section__offer {
    text-align: left; }
  .section__offer::after {
    top: 25px;
    left: 110px; } }

@media (min-width: 335px) and (max-width: 767px) {
  .section__offer::after {
    top: 7px;
    left: 310px; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .section__stuff {
    display: none; }
  .section__head {
    padding-bottom: 16px;
    padding: 23px 16px; }
  .section__servise, .section__head {
    font-size: 14px;
    display: grid;
    grid-template-columns: 400px 90px 100px 119px;
    width: 720px; }
  .section__kind {
    padding: 23px 16px; }
  .section__info {
    padding: 24px 323px 47px 0; } }

@media (min-width: 1440px) {
  .section__stuff {
    display: none; }
  .section__head {
    padding-bottom: 16px;
    padding: 16px 24px; }
  .section__servise, .section__head {
    display: grid;
    grid-template-columns: 480px 220px 220px 135px; }
  .section__kind {
    padding: 24px 23px; }
  .section__info {
    padding: 32px 550px 50px 0px; } }
