html, body, div, span,
h1, h2, h3, h4, h5, h6, p, ul, li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline; }

ul {
  list-style: none; }

a:hover, button:hover, a:focus, button:focus {
  cursor: pointer; }

a {
  text-decoration: none;
  color: #1B1C21; }

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

@font-face {
  font-family: 'ExtraBold';
  src: url(./fonts/TTLakes-ExtraBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-ExtraBold.eot) format("eot");
  src: url(./fonts/TTLakes-ExtraBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'DemiBold';
  src: url(./fonts/TTLakes-DemiBold.ttf) format("truetype");
  src: url(./fonts/TTLakes-DemiBold.eot) format("eot");
  src: url(./fonts/TTLakes-DemiBold.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'Regular';
  src: url(./fonts/TTLakes-Regular.ttf) format("truetype");
  src: url(./fonts/TTLakes-Regular.eot) format("eot");
  src: url(./fonts/TTLakes-Regular.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@media (min-width: 320px) and (max-width: 767px) {
  .footer {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 16px 0 19px 0;
    justify-content: center; }
  .footer__item {
    font-size: 12px;
    text-align: center; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .footer {
    padding: 0 24px;
    border-top: 1px solid #D9FFF5;
    display: flex;
    gap: 37px;
    align-items: center;
    height: 105px; }
  .footer__item {
    font-size: 14px; }
    .footer__item:first-child {
      white-space: nowrap; }
    .footer__item:nth-child(2n) {
      text-align: center;
      white-space: nowrap; }
    .footer__item:nth-child(3n) {
      text-align: right; } }

@media (min-width: 1440px) {
  .footer {
    padding: 0 31px;
    border-top: 1px solid #D9FFF5;
    display: flex;
    gap: 147px;
    align-items: center;
    height: 105px; }
  .footer__item {
    font-size: 14px; }
  .footer__item:first-child {
    white-space: nowrap; }
  .footer__item:nth-child(2n) {
    text-align: center;
    white-space: nowrap; }
  .footer__item:nth-child(3n) {
    text-align: right; } }
